.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #282c34;
  /* font-size: calc(14px + 3vmin); */
  font-size: 1.5rem;
  color: white;
  padding-bottom: 10px;
}

.app-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.btn-xl {
  /* font-size: calc(14px + 3vmin); */
  font-size: 2rem;
}

.text-xl {
  font-size: 1.3rem;
}

.center {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
